.header {
  width: 100%;
  height: 95px;
  background-color: #fff;
  border-bottom: 1px solid rgba(113, 111, 111, 0.537);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.379);
}

.headerRight {
  margin-right: 20px;
  align-items: center;
}

.logo {
  margin-left: 20px;
  margin-top: 8px;
}
.logo2 {
  display: none;
}

.storeisdp {
  margin: 10px;
  margin-left: 1%;
}

.storiesCard {
  width: 25%;
  margin-right: 1%;
  padding-top: 25%;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
  border-radius: 8px;
}

.borderRadios {
  width: 55px;
  height: 55px;
  background-color: #e4e6eb;
  border-radius: 50%;
  margin-right: 15px;
}

.a8c37x1j {
  margin-left: 15px;
  margin-top: 2%;
}

.borderRadios:hover {
  background-color: #d7d5d5;
  cursor: pointer;
}

.aaxa7vy3 {
  color: #1877f2 !important;
}
.card-header {
  background-color: #fff !important;
}
.card-footer {
  background-color: #fff !important;
}

.indice-content:hover {
  text-decoration: underline;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .logo {
    display: none !important;
  }
  .logo2 {
    display: block;
  }
}

.footer {
  height: 60px;
  background-color: #fff;
  margin-top: 45px;
  color: black;
  width: 100%;
  padding-top: 20px;
  border-bottom: 1px solid rgba(113, 111, 111, 0.537);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.379);
  padding-left: 15px;
  justify-content: space-between;
}
footer a {
  text-decoration: none;
  margin-right: 20px;
  color: inherit;
}

.socialIcon {
  margin-right: 20px;
  display: flex;
  gap: 11px;
  color: #d32f2f;
}
